import React from 'react';
import { motion } from 'framer-motion';

const CommunityPage = () => {
    return (
        <div className="p-6 max-w-4xl mx-auto space-y-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="space-y-4 text-center"
            >
                <span className="text-indigo-600 font-semibold tracking-wide uppercase">Community</span>
                <h1 className="text-4xl font-bold text-slate-800">Join the Conversation</h1>
                <p className="text-slate-600 text-lg">
                    Connect with thousands of other users, share ideas, and get inspired.
                </p>
            </motion.div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="bg-gradient-to-br from-indigo-500 to-purple-600 rounded-xl p-8 text-white text-center">
                    <h3 className="text-2xl font-bold mb-2">Discord Server</h3>
                    <p className="opacity-90 mb-6">Chat in real-time with the team and community.</p>
                    <button className="px-6 py-2 bg-white text-indigo-600 rounded-lg font-semibold hover:bg-opacity-90 transition-opacity">
                        Join Discord
                    </button>
                </div>
                <div className="bg-white rounded-xl p-8 border border-gray-100 text-center shadow-sm">
                    <h3 className="text-2xl font-bold mb-2 text-slate-800">Forum</h3>
                    <p className="text-gray-500 mb-6">Post questions, feature requests, and long-form discussions.</p>
                    <button className="px-6 py-2 border border-gray-300 text-gray-700 rounded-lg font-semibold hover:bg-gray-50 transition-colors">
                        Visit Forum
                    </button>
                </div>
            </div>
        </div>
    );
};

export default CommunityPage;
