import React from 'react';
import { motion } from 'framer-motion';

const HelpCenterPage = () => {
    return (
        <div className="p-6 max-w-4xl mx-auto space-y-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="space-y-4 text-center"
            >
                <h1 className="text-4xl font-bold text-slate-800">Help Center</h1>
                <p className="text-slate-600 text-lg">
                    Find answers to common questions and learn how to use Collabryta.
                </p>
                <div className="max-w-xl mx-auto">
                    <input type="text" placeholder="Search for articles..." className="w-full px-4 py-3 rounded-xl border border-gray-200 focus:outline-none focus:ring-2 focus:ring-indigo-500 shadow-sm" />
                </div>
            </motion.div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-6 pt-8">
                {['Getting Started', 'Account & Billing', 'Features & Tutorials', 'Security & Privacy', 'Troubleshooting', 'API & Developers'].map((topic) => (
                    <div key={topic} className="bg-white p-6 rounded-xl shadow-sm border border-gray-100 hover:border-indigo-100 hover:shadow-md transition-all cursor-pointer">
                        <h3 className="text-lg font-semibold text-gray-800">{topic}</h3>
                        <p className="text-sm text-gray-500 mt-2">View 12 articles</p>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default HelpCenterPage;
