import React from "react";
import { Link } from "react-router-dom";
import { ArrowLeft, Shield } from "lucide-react";

const PrivacyPolicyPage: React.FC = () => {
    return (
        <div className="min-h-screen bg-white">
            <div className="max-w-4xl mx-auto px-6 py-12 md:py-20">
                <Link to="/" className="inline-flex items-center gap-2 text-sm text-slate-500 hover:text-blue-600 mb-8 transition-colors">
                    <ArrowLeft size={16} /> Back to Home
                </Link>

                <div className="flex items-center gap-4 mb-8">
                    <div className="p-3 bg-blue-100 text-blue-600 rounded-xl">
                        <Shield size={32} />
                    </div>
                    <h1 className="text-4xl font-bold text-slate-900 tracking-tight">Privacy Policy</h1>
                </div>

                <div className="prose prose-slate max-w-none text-slate-600">
                    <p className="lead text-lg mb-6">
                        Effective Date: January 1, 2026
                    </p>
                    <p className="mb-6">
                        At Collabryta, we take your privacy seriously. This Privacy Policy explains how we collect, use,
                        disclose, and safeguard your information when you visit our website or use our collaborative
                        productivity platform ("Platform").
                    </p>

                    <h2 className="text-2xl font-bold text-slate-800 mt-10 mb-4">1. Information We Collect</h2>
                    <p className="mb-4">
                        We collect information that you provide directly to us when you register for an account, create
                        or modify your profile, set preferences, sign up for newsletters, or make other requests. This may include:
                    </p>
                    <ul className="list-disc pl-6 mb-6 space-y-2">
                        <li><strong>Account Information:</strong> Name, email address, password, and job title.</li>
                        <li><strong>Team Data:</strong> Information about your team members and organizational structure.</li>
                        <li><strong>Content:</strong> Tasks, messages, files, and other content you generate on the Platform.</li>
                    </ul>

                    <h2 className="text-2xl font-bold text-slate-800 mt-10 mb-4">2. How We Use Your Information</h2>
                    <p className="mb-4">
                        We use the information we collect to provide, maintain, and improve our services, such as to:
                    </p>
                    <ul className="list-disc pl-6 mb-6 space-y-2">
                        <li>Enable you to access and use the Platform.</li>
                        <li>Process and complete transactions, and send you related information.</li>
                        <li>Send you technical notices, updates, security alerts, and support messages.</li>
                        <li>Respond to your comments, questions, and requests.</li>
                    </ul>

                    <h2 className="text-2xl font-bold text-slate-800 mt-10 mb-4">3. Data Security</h2>
                    <p className="mb-6">
                        We implement appropriate technical and organizational measures to protect the security of your
                        personal information. However, please note that no system is completely secure.
                    </p>

                    <h2 className="text-2xl font-bold text-slate-800 mt-10 mb-4">4. Contact Us</h2>
                    <p className="mb-6">
                        If you have any questions about this Privacy Policy, please contact us at{" "}
                        <a href="mailto:privacy@collabryta.com" className="text-blue-600 hover:underline">privacy@collabryta.com</a>.
                    </p>
                </div>
            </div>
        </div>
    );
};

export default PrivacyPolicyPage;
