import React from "react";
import { Mail, MessageCircle, LifeBuoy } from "lucide-react";

const SupportPage: React.FC = () => {
  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-white px-8 py-16">
      <h1 className="text-4xl font-bold text-center mb-10 text-gray-800">Support</h1>

      <div className="max-w-3xl mx-auto text-center text-gray-600">
        <p className="text-lg mb-6">
          Need help? Our support team is always ready to assist you with setup, debugging, or
          technical questions.
        </p>

        <div className="grid md:grid-cols-3 gap-8 mt-12">
          <div className="bg-white p-6 rounded-2xl shadow-sm border hover:shadow-md transition">
            <Mail className="w-8 h-8 text-blue-600 mx-auto mb-3" />
            <h3 className="font-semibold text-lg mb-2">Email Support</h3>
            <p className="text-sm text-gray-500 mb-3">support@collabryta.com</p>
            <button className="text-blue-600 font-medium hover:underline">Send Email</button>
          </div>

          <div className="bg-white p-6 rounded-2xl shadow-sm border hover:shadow-md transition">
            <MessageCircle className="w-8 h-8 text-green-600 mx-auto mb-3" />
            <h3 className="font-semibold text-lg mb-2">Live Chat</h3>
            <p className="text-sm text-gray-500 mb-3">
              Connect instantly with our support assistant for real-time solutions.
            </p>
            <button className="text-green-600 font-medium hover:underline">Start Chat</button>
          </div>

          <div className="bg-white p-6 rounded-2xl shadow-sm border hover:shadow-md transition">
            <LifeBuoy className="w-8 h-8 text-orange-600 mx-auto mb-3" />
            <h3 className="font-semibold text-lg mb-2">Community Forum</h3>
            <p className="text-sm text-gray-500 mb-3">
              Join discussions, ask questions, and learn from other developers.
            </p>
            <button className="text-orange-600 font-medium hover:underline">Visit Forum</button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SupportPage;
