import React from 'react';
import { motion } from 'framer-motion';

const CareersPage = () => {
    return (
        <div className="p-6 max-w-4xl mx-auto space-y-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="space-y-4"
            >
                <h1 className="text-4xl font-bold text-slate-800">Careers at Collabryta</h1>
                <p className="text-slate-600 text-lg">
                    Join us in building the future of collaboration.
                </p>
            </motion.div>

            <div className="space-y-6">
                <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100">
                    <h3 className="text-xl font-semibold mb-2">Senior Full Stack Engineer</h3>
                    <p className="text-gray-500 mb-4">Remote  Engineering</p>
                    <button className="px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition-colors">
                        Apply Now
                    </button>
                </div>
                <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100">
                    <h3 className="text-xl font-semibold mb-2">Product Designer</h3>
                    <p className="text-gray-500 mb-4">Remote  Design</p>
                    <button className="px-4 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition-colors">
                        Apply Now
                    </button>
                </div>
            </div>
        </div>
    );
};

export default CareersPage;
