import React from 'react';
import { motion } from 'framer-motion';

const PressKitPage = () => {
    return (
        <div className="p-6 max-w-4xl mx-auto space-y-8">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="space-y-4"
            >
                <h1 className="text-4xl font-bold text-slate-800">Press Kit</h1>
                <p className="text-slate-600 text-lg">
                    Assets, logos, and brand guidelines for media use.
                </p>
            </motion.div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100">
                    <h3 className="text-xl font-semibold mb-2">Logos</h3>
                    <p className="text-gray-500 mb-4">Download our official logo pack (SVG, PNG, EPS).</p>
                    <button className="text-indigo-600 font-medium hover:underline">Download Assets</button>
                </div>
                <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100">
                    <h3 className="text-xl font-semibold mb-2">Brand Guidelines</h3>
                    <p className="text-gray-500 mb-4">Learn about our colors, typography, and usage rules.</p>
                    <button className="text-indigo-600 font-medium hover:underline">View Guidelines</button>
                </div>
            </div>
        </div>
    );
};

export default PressKitPage;
